.taskati-sidebar {
  width: 260px;
  background: linear-gradient(180deg, #4F8CFF 0%, #6BCBFF 100%);
  color: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(44,62,80,0.10);
  display: flex;
  flex-direction: column;
  min-height: 96vh;
  padding: 24px 0 0 0;
  margin: 16px 0 16px 16px;
}
.sidebar-header {
  padding: 0 32px 24px 32px;
  margin-bottom: 10px;
}
.sidebar-appname {
  font-size: 2rem;
  font-weight: 900;
  color: #fff;
  letter-spacing: 1px;
  margin: 0;
}
.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 0;
  margin-bottom: auto;
}
.sidebar-nav-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
  padding: 14px 32px;
  color: #fff;
  text-decoration: none;
  border-radius: 10px;
  font-weight: 500;
  font-size: 1.08rem;
  transition: background 0.18s, color 0.18s;
  margin: 2px 0;
}
.sidebar-nav-item.active, .sidebar-nav-item:hover {
  background: rgba(255,255,255,0.18);
  color: #fff;
}
.sidebar-nav-item i {
  font-size: 1.2rem;
  width: 22px;
  text-align: center;
}
.sidebar-pro-card {
  background: linear-gradient(135deg, #6BCBFF 0%, #4F8CFF 100%);
  border-radius: 14px;
  margin: 32px 24px 24px 24px;
  padding: 20px 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2px 8px rgba(90,94,227,0.10);
}
.pro-avatar {
  width: 54px;
  height: 54px;
  background: #fff;
  color: #4F8CFF;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
}
.pro-info {
  text-align: center;
  margin-bottom: 10px;
}
.pro-title {
  font-size: 1.1rem;
  font-weight: 700;
  color: #fff;
}
.pro-desc {
  font-size: 0.95rem;
  color: #eaf6ff;
  margin-top: 2px;
}
.get-pro-btn {
  background: #fff;
  color: #4F8CFF;
  border: none;
  border-radius: 8px;
  padding: 8px 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 8px;
  transition: background 0.2s, color 0.2s;
}
.get-pro-btn:hover {
  background: #eaf6ff;
  color: #4F8CFF;
}
@media (max-width: 900px) {
  .taskati-sidebar {
    width: 100vw;
    min-width: 0;
    margin: 0;
    border-radius: 0 0 18px 18px;
    box-shadow: 0 2px 8px rgba(44,62,80,0.10);
  }
} 